<template>
  <div class="kaoqin">
    <div class="nav-bar-white">
      <headerBar :header-data="headerData" />
    </div>
    <div class="kaoqin-head">
      <div class="kaoqin-userinfo-bg" />
      <div class="kaoqin-userinfo">
        <div class="userinfo-tx">
          <img src="@/assets/images/default-tx.png">
        </div>
        <div class="userinfo-name">
          <div class="name">{{ userInfo.name }}</div>
          <div class="info-flex">
            <div class="info">
              <i class="iconfont icondailijigou" />
              {{ userInfo.bmmc }}
            </div>
            <div class="info">
              <i class="iconfont iconshouji" />
              {{ userInfo.sj }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="kaoqin-time"><i class="iconfont iconalarm_clock" /> <em>当前日期：</em>{{ date }}</div>
    <div class="content">
      <div class="select-workway">

        <dl @click="goTbGzrz()">
          <dt><img src="@/assets/erp-images/icon-tbgzrz.png"></dt>
          <dd>填报工作日志</dd>
          <i class="iconfont iconarrowright" />
        </dl>
        <!-- <dl @click="goLlGzrz()">
          <dt><img src="@/assets/erp-images/icon-llgzrz.png"></dt>
          <dd>浏览工作日志</dd>
          <i class="iconfont iconarrowright" />
        </dl> -->
        <dl @click="goShGzrz()">
          <dt><img src="@/assets/erp-images/icon-shgzrz.png"></dt>
          <dd>审核工作日志</dd>
          <i class="iconfont iconarrowright" />
        </dl>

      </div>
    </div>

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'Gzrz',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      date: '',
      headerData: {
        title: '工作日志',
        leftUrl: 'MhHome',
        leftText: ' '
      },
      userInfo: {}

    }
  },
  created() {
    this.$store.commit('erp/SET_ISFIRST', true)
    this.$store.commit('erp/SET_ISSUBMIT', false)
    this.$store.commit('erp/SET_RQ', '请选择日期')
    this.$store.commit('erp/SET_CHECKINDEX', '')
    this.$store.commit('erp/SET_LIST', '')
    this.$store.commit('erp/SET_CHECKXM', '')
    const _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    _this.date = _this.getNowTime()
    this.timer = setInterval(() => {
      _this.date = _this.getNowTime() // 修改数据date
    }, 1000)
    this.userInfo = this.$store.state.mh.mhUserInfo
  },
  methods: {
    getNowTime() {
      const date = new Date()
      let hours = ''
      let minute = ''
      let second = ''
      if (date.getHours() < 10) {
        hours = '0' + date.getHours()
      } else {
        hours = date.getHours()
      }
      if (date.getMinutes() < 10) {
        minute = '0' + date.getMinutes()
      } else {
        minute = date.getMinutes()
      }
      if (date.getSeconds() < 10) {
        second = '0' + date.getSeconds()
      } else {
        second = date.getSeconds()
      }
      return date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日' + '  ' + hours + ':' + minute + ':' + second
    },
    goTbGzrz() {
      this.$router.push({
        name: 'TbGzrz'
      })
    },
    goLlGzrz() {
      this.$router.push({
        name: 'LlGzrz'
      })
    },
    goShGzrz() {
      this.$router.push({
        name: 'ShGzrzYl'
      })
    }
  }
}
</script>

<style scoped>

</style>
